import { useEffect, useState } from 'react';
import { useGameContext } from '@src/contexts/GamesContext';
export function useTransformGamesData() {
    const [transformedGamesData, setTransformedGamesData,] = useState();
    const { gamesList, isLoading } = useGameContext();
    function transformGameData(list) {
        const transformedData = Object.entries(list)
            .reduce((acc, [gameId, gameData]) => {
            const data = {
                id: gameId,
                title: gameData.title,
                provider: gameData.provider,
                demo: gameData.demo,
            };
            acc.push(data);
            return acc;
        }, []);
        setTransformedGamesData(transformedData);
    }
    useEffect(() => {
        if (gamesList) {
            transformGameData(gamesList);
        }
    }, [gamesList]);
    return {
        isLoading,
        gamesData: transformedGamesData,
    };
}
